export default {
	data() {
		return {
			popup: false,
			tourist: false,
			wxLogin: true,
			activityData: [],
			ImgUrl: this.ImgUrl,
			location: [],
			locationIndex: 0,
			locationId: {},
			space: {},
			popupChange: '',
			headShow: false,
			type: {},
			view: {}
		}
	},
	onLoad() {
		this.getUser()
		
		this.getArea()
		
		this.getWe()
		
		this.getSpace()
		
		
		uni.setStorage({
			key: 'tourist',
			data: false
		})
	},
	methods: {
		// login
		Login() {
			let baseUrl = this.WebUrl
			let that = this
			uni.login({
				provider: 'weixin',
				success: (Lg) => {
					let code = Lg.code
					uni.request({
						url: baseUrl + '/wxLogin',
						data: {
							code: code
						},
						method: 'GET',
						header: {
							'content-type': 'application/json'
						},
						success: (id) => {
							console.log(id)
							uni.setStorage({
								key: 'openid',
								data: id.data.data.openid,
								success:function(){
									that.getUser()
								}
							})
						}
					})
		
				}
			})
		},
		
		// 判断用户是否存在
		getUser() {
			let baseUrl = this.WebUrl
			let that = this
			uni.getStorage({
				key: 'openid',
				success: function(id){
					console.log(id.data)
					uni.request({
						url: baseUrl + '/judgeUser',
						method: 'POST',
						data: {
							openid: id.data
						},
						success: (res) => {
							if (res.data.msg === "用户已存在！"){
								that.tourist = true
								that.wxLogin = false
								uni.setStorage({
									key: 'u_id',
									data: res.data.data.u_id
								})
								
								uni.setStorage({
									key: 'tourist',
									data: true
								})
							}
						}
					})
				},
				fail:function(err){
					that.Login()
				}
			})
		},
		
		// 授权登录
		getUserInfo(res) {
			let that = this
			let baseUrl = this.WebUrl
			let area_id = 0
			for(let key in this.locationId){
				console.log(key)
				if(key === this.location[this.locationIndex]){
					area_id = this.locationId[key].id
				}
			}
		
			uni.getUserInfo({
				provider: "weixin",
				success: (res) => {
					let data = {
						nickName: res.userInfo.nickName,
						avatarUrl: res.userInfo.avatarUrl,
						gender: res.userInfo.gender,
						city: res.userInfo.city
					}
					
					uni.getStorage({
						key: 'openid',
						success:function(id) {
							uni.request({
								url: baseUrl + '/addUser',
								method: "POST",
								data: {
									area_id: area_id,
									city: data.city,
									gender: data.gender,
									nickName: data.nickName,
									avatarUrl: data.avatarUrl,
									openid: id.data
								},
								success(resd) {
									console.log(resd)
									that.getUser()
								}
							})
						}
					})
				},
				fail: (err) => {
					console.log(err)
				}
			})
		},
		
		// 头部跳转
		webView(url) {
			console.log(url)
			uni.showModal({
				title:"该地址无法跳转",
				showCancel: false
			})
			// this.headShow = true
			// this.popupChange === url
		},
		
		// 获取区域
		getArea() {
			let baseUrl = this.WebUrl
			let that = this
			uni.request({
				url: baseUrl + '/getArea',
				success(res) {
					res.data.data.forEach(function(item){
						that.location.push(item.dict_name)
						that.locationId[item.dict_name] = {id:item.dict_id}
					})
					that.getActivity()
					that.getHot()
					uni.getStorage({
						key: 'area_id',
						success:(res) => {
							console.log(res)
						},
						fail:() => {
							console.log(res.data.data[0].dict_id)
							uni.setStorage({
								key: "area_id",
								data: res.data.data[0].dict_id
							})
						}
					})
					
				}
			})
		},
		
		// 获取关于我们
		getWe() {
			let baseUrl = this.WebUrl
			let that = this
			uni.request({
				url: baseUrl + '/getWe',
				success(res) {
					that.view = res.data.data
					console.log(res.data.data)
					console.log('关于我们')
				}
			})
		},
		
		// 获取空间类型
		getSpace() {
			let baseUrl = this.WebUrl
			let that = this
			uni.request({
				url: baseUrl + '/getSpace',
				success(res) {
					that.type = res.data.data
				}
			})
		},
		
		// 获取热推空间
		getHot() {
			let baseUrl = this.WebUrl
			let that = this
			let area_id
			for(let key in this.locationId){
				console.log(key)
				if(key === this.location[this.locationIndex]){
					area_id = this.locationId[key].id
				}
			}
			
			uni.request({
				url: baseUrl + '/getHot',
				data: {
					area_id: area_id
				},
				success(res) {
					that.space = res.data.data
					console.log(res)
					console.log('aaa')
				}
			})
		},
		
		// 获取活动
		getActivity() {
			let baseUrl = this.WebUrl
			let that = this
			let area_id
			for(let key in this.locationId){
				console.log(key)
				if(key === this.location[this.locationIndex]){
					area_id = this.locationId[key].id
				}
			}
			uni.request({
				url: baseUrl + '/getActivity',
				data: {
					area_id: area_id
				},
				success(res) {
					that.activityData = res.data.data
					console.log(that.activityData)
				}
			})
		},
		
		// 切换市
		locationChange(e) {
			this.locationIndex = e.target.value
			this.getActivity()
			let area_id
			for(let key in this.locationId){
				if(key === this.location[this.locationIndex]){
					area_id = this.locationId[key].id
				}
			}
			console.log(area_id)
			uni.setStorage({
				key: "area_id",
				data: area_id
			})
		},
		
		// 点开关于我们
		aboutUsButton() {
			console.log(1)
			if(this.popup) {
				this.popup = false
			}else {
				this.popup = true
			}
		},
		
		// 跳转场地合作
		toField() {
			uni.navigateTo({
				url: '../home/field'
			});
		},
		
		// 跳转预约参观
		toVisit() {
			if (this.tourist) {
				uni.navigateTo({
					url: '../home/visit'
				});
			}else {
				uni.showModal({
					title: "提示",
					content: "请先授权登录",
					success: (res) => {
						if (res.confirm) {
							this.wxLogin = true
						}
					}
				})
			}
		},
		
		// 跳转入驻申请
		toApply() {
			if (this.tourist) {
				uni.navigateTo({
					url: '../home/apply'
				});
			}else {
				uni.showModal({
					title: "提示",
					content: "请先授权登录",
					success: (res) => {
						if (res.confirm) {
							this.wxLogin = true
						}
					}
				})
			}
		},
		
		// 跳转热推空间
		toSpace(id) {
			console.log(this.space[id].infor_id)
			uni.navigateTo({
				url: '../home/space?infor_id=' + this.space[id].infor_id
			})
		},
		
		// 跳转活动界面
		activity(id) {
			uni.navigateTo({
				url: '../home/activity?id=' + id
			});
		},
		
		// 点击遮罩
		changePopup() {
			this.popup = false
		},
		
		// 授权遮罩
		changeWxLogin() {
			if(this.wxLogin === true){
				this.wxLogin = false
			}else {
				this.wxLogin = true
			}
		}
	}
}
